/* latin */
@font-face {
    font-family: 'Product Sans';
    font-style: normal;
    font-weight: 700;
    src: local('Product Sans Bold'), local('ProductSans-Bold'), url(https://fonts.gstatic.com/s/productsans/v9/pxicypQkot1TnFhsFMOfGShdrPKbT2lBgGygpg.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

$charcoal: #36454f;
$grayblue: #1f2833;
$alternateBackgroundColor: #0b0c10;
$borderColor: #d7e4ed;
$breakpointLarge: 1170px;
$breakpointSmall: 796px;
$breakpointXSmall: 500px;
body,html {
    height: 100%;
    width: 100%;
    font-family: 'Product Sans';
    font-weight: 400;
}
body {
    margin: 0 !important;
}
.container {
    width: 100%;
    height: 100%;
}
.section {
  //  padding: 50px 0px 50px 30px;
    &__container {
        max-width: 992px;
        padding: 70px 0 0 0;
        margin: 0 auto;
        @media screen and (max-width: $breakpointSmall) {
            padding: 50px 0 0 0;
            width: 90%;
        }
    }
  //  border-bottom: 1px solid $borderColor;
    background-color: $alternateBackgroundColor;
    &--header {
        font-size: 28px;
        margin-bottom: 25px;
        width: 100%;
        color: white;
        &__text-center {
            display: block;
        }
    }
    &__about {
        color: white;
         &--text {
             width: 100%;
             font-size: 16px;
             line-height: 1.4em;

             > a {
                 color: white !important;
                 font-weight: 600;
                 &:hover{
                     cursor: pointer;
                 }
             }
         }
        &__projects {
            margin-top: 30px;
            >a {
                color: white;
                font-weight: 500;
                font-size: 18px;
                text-decoration: none;
                display: inline-block;
                vertical-align: middle;
                // border: 1px solid white;
                padding: 8px;
                margin: 5px;
                border-radius: 3px;
                box-shadow: 27px 30px 55px -17px rgba(255,255,255,0.08);
                background-color: $grayblue;
                &:hover {
                    background-color: $grayblue;
                }
            }
        }
        &__education {
            box-shadow: 27px 30px 55px -17px rgba(255,255,255,0.08);
            background-color: $grayblue;
            padding: 10px;
            border-radius: 3px;
            margin-top: 30px;
            width: 100%;
            &--header {
                padding: 15px;
                font-size: 24px;
            }
            &--item {
                padding: 5px 15px;
                width: 100%;
             //   color: $charcoal;
                &__school {
                    display: block;
                    font-size: 16px;
                    padding-bottom: 5px;
                    a {
                        color: white !important;
                        text-decoration: none;
                        cursor: pointer;
                        font-weight: 600;
                    }
                    &--course {
                        @media screen and (max-width: $breakpointXSmall) {
                            display: none;
                        }
                        &-mobile {
                            @media screen and (max-width: $breakpointXSmall) {
                                font-size: 14px;
                                display: block;
                                padding-top: 5px;
                                padding-bottom: 5px;
                            }
                            @media screen and (min-width: $breakpointXSmall) {
                                display: none;
                            }
                        }
                    }
                }
                &__text {
                    display: block;
                    font-size: 16px;
                    padding: 10px 0;
                    &--nopadding {
                        padding: 4px;
                    }
                }
                &__duration {
                    display: block;
                    font-size: 14px;
                    .fa {
                        padding-right: 5px;
                    }
                    @media screen and (min-width: $breakpointXSmall) {
                        padding-top: 5px;
                    }
                }
            }
        }
    }
    &__intro {
        background: url('../images/cover.JPG') no-repeat center fixed;
        background-size: cover;
        background-color: $alternateBackgroundColor;
        height: 100vh;
        padding: 0;
        &--text {
            position: relative;
            width: 100%;
            top: 50%;
            transform: translateY(-50%);
            text-align: center;
            &__name {
                color: white;
                width: 500px;
                font-size: 88px;
                padding: 10px;
                font-weight: 300;
                overflow: hidden; /* Ensures the content is not revealed until the animation */
                border-right: 1px solid white; /* The typwriter cursor */
                white-space: nowrap; /* Keeps the content on a single line */
                margin: 0 auto; /* Gives that scrolling effect as the typing happens */
                animation: 
                    typing 3.5s steps(20, end),
                    blink-caret .75s step-end infinite;
                @media screen and (max-width: $breakpointSmall) {
                    font-size: 40px;
                    width: 250px;
                }
            }
            &__sub {
                display: block;
                font-size: 20px;
                color: white;
            }
            &__btn-grp {
                padding: 10px;
                .fa {
                    padding: 2px;
                    color: white !important;
                    &:hover {
                        color: #e5e5e5 !important;
                        border-bottom: 1px solid #e5e5e5;
                        cursor: pointer;
                    }
                }
            }
        }
    }
    &__work {
        @media screen and (max-width: $breakpointXSmall) {
            padding-left: 0em !important;
            padding-right: 0em !important;
        }
        &--header {
            font-size: 22px;
            font-weight: 600;
        }
        &--position {
            font-size: 20px;
            font-weight: 400;
            @media screen and (max-width: $breakpointXSmall) {
                    display: none;
                }
            
            &-mobile {
                @media screen and (max-width: $breakpointXSmall) {
                    font-size: 16px;
                    display: block;
                    padding-top: 5px;
                    font-weight: 600;
                 }
                @media screen and (min-width: $breakpointXSmall) {
                    display: none;
                }
            }
        }
        &--item {
            display: block;
            line-height: 1.2em;
            padding-bottom: 8px;
            .fa {
                padding-right: 5px;
            }
            &:nth-child(4) {
                padding-top: 10px;
            }
        }
    }
    &__skills {
        color: white;
        &__group {
            box-shadow: 27px 30px 55px -17px rgba(255,255,255,0.08);
            background-color: $grayblue;
            padding: 10px;
            border-radius: 3px;
            margin-top: 30px;
            width: 100%;
            &--header {
                padding-left: 15px;
                margin-bottom: 10px;
                display: block;
                font-size: 26px;
            }
            &--item {
                display: table;
                padding: 5px 15px;
                font-size: 16px;
                width: 100%;
                > span {
                    color: white !important;
                    display: table-cell;
                    width: 120px;
                    text-decoration: none !important;
                }
                > .progress-bar {
                    display: table-cell;
                    width: calc(100% - 120px);
                    position: relative;
                    > svg {
                        padding-bottom: 5px;
                        height: 8px;
                    }
                }
            }
        }
        &__frameworks {
            padding: 30px 0 0 0;
            &__header {
                font-size: 1.2em;
            }
            > div {
                padding-top: 10px;
                line-height: 2.8;
                > span {
                    padding: 5px 10px;
                    box-shadow: 27px 30px 55px -17px rgba(255,255,255,0.08);
                    background-color: $grayblue;
                    border-radius: 3px;
                    margin-right: 10px;
                }
            }
        }
    }
}

.download-cv {
    position: fixed;
    bottom: 20px;
    right: 20px;
    height: 40px;
    width: 120px;
    font-family: 'Product Sans';
    font-size: 16px;
    cursor: pointer;
    text-align: center;
    line-height: 40px;
    outline: none;
    z-index: 100;
    box-shadow: 27px 30px 55px -17px rgba(255,255,255,0.08);
}

.button-blue {
    background-color: $grayblue !important;
    border: 1px solid $charcoal;
    text-decoration: none;
    color: white;
    &:hover {
        background-color: rgba(54,57,79,0.9);
    }
}   
footer {
    position: relative;
    height: 150px;
    background-color: $alternateBackgroundColor;
    color: white;
    z-index: 1;
    > div {
        text-align: center;
        padding: 50px;
        .fa {
            padding: 2px;
            color: white !important;
            &:hover {
                color: $grayblue !important;
                border-bottom: 1px solid $grayblue;
                cursor: pointer;
            }
        }
    }
    > span {
        text-align: center;
        width: 100%;
        display: block;
        position: absolute;
        bottom: 15px;
        font-size: 12px;
    }

}
@keyframes typing {
  from { width: 0 }
}

@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: white; }
}
/* -------------------------------- 

Primary style

-------------------------------- */
html * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*, *:after, *:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

/* -------------------------------- 

Modules - reusable parts of our design

-------------------------------- */
.cd-container {
  /* this class is used to give a max-width to the element it is applied to, and center it horizontally when it reaches that max-width */
  width: 90%;
  max-width: $breakpointLarge;
  margin: 0 auto;
}
.cd-container::after {
  /* clearfix */
  content: '';
  display: table;
  clear: both;
}

/* -------------------------------- 

Main components 

-------------------------------- */

@media only screen and (min-width: $breakpointLarge) {
  header {
    height: 300px;
    line-height: 300px;
  }
  header h1 {
    font-size: 24px;
    font-size: 1.5rem;
  }
}

#cd-timeline {
  position: relative;
  padding: 2em 0;
}
#cd-timeline::before {
  /* this is the vertical line */
  content: '';
  position: absolute;
  top: 0;
  left: 18px;
  height: 100%;
  width: 1px;
  background: $borderColor;
}
@media only screen and (min-width: $breakpointLarge) {
  #cd-timeline::before {
    left: 50%;
    margin-left: -2px;
  }
}

.cd-timeline-block {
  position: relative;
  margin: 2em 0;
}
.cd-timeline-block:after {
  content: "";
  display: table;
  clear: both;
}
.cd-timeline-block:first-child {
  margin-top: 0;
}
.cd-timeline-block:last-child {
  margin-bottom: 0;
}
@media only screen and (min-width: $breakpointLarge) {
  .cd-timeline-block {
    margin: 4em 0;
  }
  .cd-timeline-block:first-child {
    margin-top: 0;
  }
  .cd-timeline-block:last-child {
    margin-bottom: 0;
  }
}

.cd-timeline-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  box-shadow: 0 0 0 1px white, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);
}
.cd-timeline-img img {
  display: block;
  width: 24px;
  height: 24px;
  position: relative;
  left: 50%;
  top: 50%;
  margin-left: -12px;
  margin-top: -12px;
}
.cd-timeline-img.cd-right {
  background: $grayblue;
}
.cd-timeline-img.cd-left {
  background: black;
}

@media only screen and (min-width: $breakpointLarge) {
  .cd-timeline-img {
    width: 60px;
    height: 60px;
    left: 50%;
    margin-left: -30px;
    /* Force Hardware Acceleration in WebKit */
    -webkit-transform: translateZ(0);
    -webkit-backface-visibility: hidden;
  }
}


.cd-timeline-content {
  position: relative;
  margin-left: 60px;
  color: white;
  border-radius: 0.5em;
  padding: 1em;
  box-shadow: 27px 30px 55px -17px rgba(255,255,255,0.08);
  background-color: $grayblue;
}
.cd-timeline-content:after {
  content: "";
  display: table;
  clear: both;
}
.cd-timeline-content h2 {
  color: #303e49;
}
.cd-timeline-content p, .cd-timeline-content .cd-read-more, .cd-timeline-content .cd-date {
  font-size: 13px;
  font-size: 0.8125rem;
  > i {
      padding-right: 5px;
  }
}
.cd-timeline-content .cd-read-more, .cd-timeline-content .cd-date {
  display: inline-block;
}
.cd-timeline-content p {
  margin: 1em 0;
  line-height: 1.6;
}
.cd-timeline-content .cd-read-more {
  float: right;
  padding: .8em 1em;
  background: #acb7c0;
  color: white;
  border-radius: 0.25em;
}
.no-touch .cd-timeline-content .cd-read-more:hover {
  background-color: #bac4cb;
}
.cd-timeline-content .cd-date {
  float: left;
  padding: .8em 0;
  opacity: .7;
}
.cd-timeline-content::before {
  content: '';
  position: absolute;
  top: 16px;
  right: 100%;
  height: 0;
  width: 0;
  border: 7px solid transparent;
  border-right: 7px solid $borderColor !important;
}
@media only screen and (min-width: 768px) {
  .cd-timeline-content h2 {
    font-size: 20px;
    font-size: 1.25rem;
  }
  .cd-timeline-content p {
    font-size: 16px;
    font-size: 1rem;
  }
  .cd-timeline-content .cd-read-more, .cd-timeline-content .cd-date {
    font-size: 14px;
    font-size: 0.875rem;
  }
}
@media only screen and (min-width: $breakpointLarge) {
  .cd-timeline-content {
    margin-left: 0;
    padding: 1.6em;
    width: 45%;
  }
  .cd-timeline-content::before {
    top: 24px;
    left: 100%;
    border-color: transparent;
    border-left-color: $borderColor;
    border-right-color: transparent !important;
  }
  .cd-timeline-content .cd-read-more {
    float: left;
  }
  .cd-timeline-content .cd-date {
    position: absolute;
    width: 100%;
    left: 122%;
    top: 6px;
    font-size: 16px;
    font-size: 1rem;
    margin-top: 0 !important;
  }
  
  
  .cd-timeline-block:nth-child(even) .cd-timeline-content {
    float: right;
  }

  .cd-timeline-block:nth-child(even) .cd-timeline-content::before {
    top: 24px;
    left: auto;
    right: 100%;
    border-color: transparent;
    border-right-color: $borderColor !important;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-read-more {
    float: right;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-date {
    left: auto;
    right: 122%;
    text-align: right;
  }
}

.cd-read-more {
    margin-top: 10px;
    @media screen and (max-width: $breakpointXSmall) {
        display: none !important;
    }
}

.cd-date {
    color: white;
    opacity: 1 !important;
    @media screen and (max-width: $breakpointXSmall){
        margin-top: 0px !important;
    }
    @media screen and (max-width: $breakpointLarge){
        margin-top: 10px;
    }
    @media screen and (min-width: $breakpointLarge){
        opacity: 1 !important;
    }
}

